$text-color: #4e0c62;
$text-hover: #340842;
$lavender: #AAA0E7;
$name-purple: #7C6ED8;
$tan: #FFE2C9;
$green: #AEEDB5;
$blue: #BAF1FC;
$link-blue: #2f89b3;
$pink: #FFC4E2;
$white: #f8f8f8;

@mixin flex($direction,$justify) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
}

html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}
*, *:before, *:after {
  box-sizing: border-box;
}
body{
  font-family: 'Gaegu', cursive;
  color: $text-color;
  margin: 0;
  position: relative;
}

.App {
  overflow-x: hidden;
}

h1,h2,h3,h4 {
  position: relative;
  margin: 15px 0;
  color: $text-color;
}

h1 {
  font-size: 60px;
  font-weight: 500;
}

p{
  font-size: 20px;
}

img {
  position: relative;
  width: 100%;
}

h3 {
  text-align: center;
  font-size: 24px;
}

#straight {
  visibility: hidden
}

p,a {
  font-size: 20px;
}

.title {
  width: auto;
  height: 100px;
}

.content-wrap {
  position: relative;
  max-width: 1100px;
  margin: 0 6%;
  padding-top: 30px;
  overflow: visible;
  width: 100%;
}

.divider {
  z-index: -1;
}

nav {
  background: $lavender;
  @include flex(row,center);
  width: 100%;
  height: 110px;
  margin: auto;
  position: sticky;

  #NavWiggle {
    visibility: hidden;
  }

  .content-wrap {
    margin: 0;
    padding: 0;
    a{
      display: flex;
      justify-content: center;
      align-items: flex-end;
      transform: translateY(-180px);
      position: relative;
      width: 20%;
      max-width: 200px;
      text-decoration: none;
      transition: 0.3s ease-out;
      animation: 0.5s flyIn ease-out forwards;

      &:hover, &:focus-visible {
        transform: translateY(5px);
        color: $text-hover;
      }

      &:focus-visible {
        outline: none;

        path {
          stroke: $link-blue;
        }
      }

      h2 {
        width:100%;
        font-weight: 500;
      }

      svg {
        left: 0;
        top: 0;
        position: absolute;
        width: 100%;

        path {
          stroke: $text-color;
          stroke-width: 8px;
        }
      }
    }

    #WhoNav {
      animation-delay: 0s;
      svg {
        fill: $tan;
      }
    }

    #ProjectsNav {
      animation-delay: 0.15s;
      svg {
        fill: $green;
      }

      h2 {
        margin-left: -5px;
      }
    }

    #UXNav {
      animation-delay: 0.3s;
      svg {
        fill: $blue;
      }

      h2 {
        margin-left: -10px;
      }
    }

    #ContactNav {
      animation-delay: 0.4s;
    }

    @include flex(row,space-around);
    max-width: 900px;
  }
}

#star, #NavArrow {
  visibility:hidden;
}

header {
  background: $lavender;
  @include flex(row,center);
  .content-wrap {
    @include flex(row,center);
    margin: 20px 0 -2vw 0;
    padding: 0;
    z-index: 100;
  }

  svg {
    margin: auto;
    width: 90vw;
    height: 50vw;
    max-width: 700px;
    max-height: 389px;
    background-size: cover;
    position: relative;

    $name-time: 1s;

    path {
      stroke-width: 2.5;
      stroke-dasharray: 320;
      stroke-dashoffset: 320;
      animation: dash $name-time ease-out forwards;
    }
   
    circle {
      fill: $name-purple;
      r: 0;
      animation: pulse $name-time ease-out forwards;
    }

    .circle.bg {
      fill: $name-purple;
    }

    .star-wrapper {
      overflow: visible;
      animation: fadeIn 2*$name-time ease-out forwards;
    }

    .i, .l1, .h {
      animation-delay: $name-time/5;
    };

    .l2, .e, .n2 {
      animation-delay: 2*$name-time/5;
    }

    .o, .r {
      animation-delay: 3*$name-time/5;
    }

    .n1, .y {
      animation-delay: 4*$name-time/5;
    }

    @keyframes flyIn {
      0% {
        transform: translateY(-180px);
      }
      100% {
        transform: translateY(-30px);
      }
    }

    @keyframes fadeIn {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }

    @keyframes pulse {
      0% {
        r: 0;
      }
      50% {
        r: 10;
      }
      100% {
        r: 6;
      }
    }

    @keyframes dash {
      from {
        stroke-dashoffset: 320;
      }
      to {
        stroke-dashoffset: 0;
      }
    }
  }
}

.dot {
  will-change: transform, background-color;
  transform-origin: center;
  background-color: #7568CC;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  position: relative;
  transform: translate(0);
}

section {
  position: relative;
  @include flex(column,center);
  align-items: center;
  .content-wrap {
    @include flex(row,center);
    margin: 0;
    overflow: visible;
  }
}

#who {
  background: $tan;
  margin-top: -3vw;
  .content-wrap {
    justify-content: center;
    align-items: center;
    width: 85%;
    margin: 0 30px;
    #Portrait {
      margin: 15px 5vw;
      width: 30vw;
      max-width: 280px;
      min-width: 170px;
    }
    p{
      width: 40vw;
      max-width: 600px;
    }

  }
}

#projects {
  background: $green;
  .content-wrap{
    margin: 0;
    flex-flow: row wrap;
  }
}

.project {
  background: $white;
  border: 4px solid $lavender;
  padding: 25px;
  margin: 20px 30px;
  @include flex(column, top);
  width: 80%;
  max-width: 300px;
  border-radius: 8px;
  transition: 0.2s ease-out;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

  &:hover {
    transform: scale(1.05);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  }

  img {
    width: 60%;
  }

  #SeamCarving {
    filter: brightness(1.7);
  }

  h3 {
    font-size: 30px;
  }

  p, .footer-link {
    font-family: "Fredoka", sans-serif;
    font-optical-sizing: auto;
  }

  p {
    font-size: 16px;
  }

  a.footer-link {
    padding: 10px 0;
    font-size: 17px;
    text-decoration: none;
    color: $link-blue;

    &:hover {
      filter: brightness(0.7);
    }

    .arrow:after {
      content: "";
      display: inline-block !important;
      width: 0;
      height: 0;
      border-left: 6px solid $link-blue;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
      vertical-align: middle;

    }

    .arrow:before {
      width: 8px;
      height: 3px;
      background: $link-blue;
      content: "";
      display: inline-block;
      vertical-align: middle;
      margin-left: 5px;
    }
  }

  .languages {
    margin: 5px 0;
    user-select: none;
    span {
      border: 1px solid $lavender;
      padding: 4px 8px;
      border-radius: 4px;
      margin: 0 5px;
    }
  }
}

#contact {
  background: $pink;
  .content-wrap {
    margin: 30px 30px 20px 30px;
    padding-bottom: 50px;
    img{
      width: 50%;
      max-width: 300px;
    }

    .col{
      @include flex(row,center);
    }
  }
}

#Research {
  background: $blue;
  .content-wrap {
    margin: 0;
    flex-flow: row wrap;
    .interest {
      @include flex(column,center);
      align-items: center;
      img {
        width: 75%;
        max-width: 200px;
      }
    }
  }
}

@media(max-width: 769px){
  .title{
    height: 75px;
    margin-top: -5px;
  }
  p{
    font-size: 18px;
  }

  h1 {
    font-size: 45px;
    margin: 10px 0;
  }
  nav{
    min-height: 60px;
    height: 18vw;
    margin: 0;

    .content-wrap {
      a{
        min-width: 70px;
        align-items: center;
        width: 24%;
        animation: none;
        transform: translateY(-10px);
      }
    }

    h2 {
      font-size: 5vw;
      margin: 10px 0;
    }
  }

  header {
    .content-wrap {
      margin: 0;
    }
  }

  #who {
    .content-wrap {
      padding-top: 0;
      flex-direction: column;
      p{
        width: 80vw;
        text-align: center;
      }
    }
  }

  .project {
    margin: 10px;
  }

  #CaseStudies {
    .content-wrap{
      .col {
        width: 200px;
        img{
          width: 150px;
        }
      }
    }
  }
}

@media(max-width: 660px) {
  .project {
    max-width: 350px;
  }
}

#HeaderWave {
  background: $lavender;
}

#WhoWave {
  background: $tan;
}

#DevWave {
  background: $green; 
}

#UxWave {
  background: $blue;
}

.wave-container {
  position: relative;
  width: 105%;
  margin-left: -5px;

  svg {
    margin-bottom: -8vw;
    stroke: $text-color;
    stroke-width: 2;
  }
}

/* Animations */

@keyframes nav_slide{
  0%{
    transform: translateY(-15px);
  }
  100%{
    transform: translateY(0px);
  }
}

@keyframes nav_out{
  0%{
    transform: translateY(0px);
  }
  100%{
    transform: translateY(-15px);
  }
}

