html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  font-family: "Gaegu", cursive;
  color: #4e0c62;
  margin: 0;
  position: relative;
}

.App {
  overflow-x: hidden;
}

h1, h2, h3, h4 {
  position: relative;
  margin: 15px 0;
  color: #4e0c62;
}

h1 {
  font-size: 60px;
  font-weight: 500;
}

p {
  font-size: 20px;
}

img {
  position: relative;
  width: 100%;
}

h3 {
  text-align: center;
  font-size: 24px;
}

#straight {
  visibility: hidden;
}

p, a {
  font-size: 20px;
}

.title {
  width: auto;
  height: 100px;
}

.content-wrap {
  position: relative;
  max-width: 1100px;
  margin: 0 6%;
  padding-top: 30px;
  overflow: visible;
  width: 100%;
}

.divider {
  z-index: -1;
}

nav {
  background: #AAA0E7;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 110px;
  margin: auto;
  position: sticky;
}
nav #NavWiggle {
  visibility: hidden;
}
nav .content-wrap {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  max-width: 900px;
}
nav .content-wrap a {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  transform: translateY(-180px);
  position: relative;
  width: 20%;
  max-width: 200px;
  text-decoration: none;
  transition: 0.3s ease-out;
  animation: 0.5s flyIn ease-out forwards;
}
nav .content-wrap a:hover, nav .content-wrap a:focus-visible {
  transform: translateY(5px);
  color: #340842;
}
nav .content-wrap a:focus-visible {
  outline: none;
}
nav .content-wrap a:focus-visible path {
  stroke: #2f89b3;
}
nav .content-wrap a h2 {
  width: 100%;
  font-weight: 500;
}
nav .content-wrap a svg {
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
}
nav .content-wrap a svg path {
  stroke: #4e0c62;
  stroke-width: 8px;
}
nav .content-wrap #WhoNav {
  animation-delay: 0s;
}
nav .content-wrap #WhoNav svg {
  fill: #FFE2C9;
}
nav .content-wrap #ProjectsNav {
  animation-delay: 0.15s;
}
nav .content-wrap #ProjectsNav svg {
  fill: #AEEDB5;
}
nav .content-wrap #ProjectsNav h2 {
  margin-left: -5px;
}
nav .content-wrap #UXNav {
  animation-delay: 0.3s;
}
nav .content-wrap #UXNav svg {
  fill: #BAF1FC;
}
nav .content-wrap #UXNav h2 {
  margin-left: -10px;
}
nav .content-wrap #ContactNav {
  animation-delay: 0.4s;
}

#star, #NavArrow {
  visibility: hidden;
}

header {
  background: #AAA0E7;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
header .content-wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px 0 -2vw 0;
  padding: 0;
  z-index: 100;
}
header svg {
  margin: auto;
  width: 90vw;
  height: 50vw;
  max-width: 700px;
  max-height: 389px;
  background-size: cover;
  position: relative;
}
header svg path {
  stroke-width: 2.5;
  stroke-dasharray: 320;
  stroke-dashoffset: 320;
  animation: dash 1s ease-out forwards;
}
header svg circle {
  fill: #7C6ED8;
  r: 0;
  animation: pulse 1s ease-out forwards;
}
header svg .circle.bg {
  fill: #7C6ED8;
}
header svg .star-wrapper {
  overflow: visible;
  animation: fadeIn 2s ease-out forwards;
}
header svg .i, header svg .l1, header svg .h {
  animation-delay: 0.2s;
}
header svg .l2, header svg .e, header svg .n2 {
  animation-delay: 0.4s;
}
header svg .o, header svg .r {
  animation-delay: 0.6s;
}
header svg .n1, header svg .y {
  animation-delay: 0.8s;
}
@keyframes flyIn {
  0% {
    transform: translateY(-180px);
  }
  100% {
    transform: translateY(-30px);
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pulse {
  0% {
    r: 0;
  }
  50% {
    r: 10;
  }
  100% {
    r: 6;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 320;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.dot {
  will-change: transform, background-color;
  transform-origin: center;
  background-color: #7568CC;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  position: relative;
  transform: translate(0);
}

section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
section .content-wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
  overflow: visible;
}

#who {
  background: #FFE2C9;
  margin-top: -3vw;
}
#who .content-wrap {
  justify-content: center;
  align-items: center;
  width: 85%;
  margin: 0 30px;
}
#who .content-wrap #Portrait {
  margin: 15px 5vw;
  width: 30vw;
  max-width: 280px;
  min-width: 170px;
}
#who .content-wrap p {
  width: 40vw;
  max-width: 600px;
}

#projects {
  background: #AEEDB5;
}
#projects .content-wrap {
  margin: 0;
  flex-flow: row wrap;
}

.project {
  background: #f8f8f8;
  border: 4px solid #AAA0E7;
  padding: 25px;
  margin: 20px 30px;
  display: flex;
  flex-direction: column;
  justify-content: top;
  width: 80%;
  max-width: 300px;
  border-radius: 8px;
  transition: 0.2s ease-out;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.project:hover {
  transform: scale(1.05);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.project img {
  width: 60%;
}
.project #SeamCarving {
  filter: brightness(1.7);
}
.project h3 {
  font-size: 30px;
}
.project p, .project .footer-link {
  font-family: "Fredoka", sans-serif;
  font-optical-sizing: auto;
}
.project p {
  font-size: 16px;
}
.project a.footer-link {
  padding: 10px 0;
  font-size: 17px;
  text-decoration: none;
  color: #2f89b3;
}
.project a.footer-link:hover {
  filter: brightness(0.7);
}
.project a.footer-link .arrow:after {
  content: "";
  display: inline-block !important;
  width: 0;
  height: 0;
  border-left: 6px solid #2f89b3;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  vertical-align: middle;
}
.project a.footer-link .arrow:before {
  width: 8px;
  height: 3px;
  background: #2f89b3;
  content: "";
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
}
.project .languages {
  margin: 5px 0;
  user-select: none;
}
.project .languages span {
  border: 1px solid #AAA0E7;
  padding: 4px 8px;
  border-radius: 4px;
  margin: 0 5px;
}

#contact {
  background: #FFC4E2;
}
#contact .content-wrap {
  margin: 30px 30px 20px 30px;
  padding-bottom: 50px;
}
#contact .content-wrap img {
  width: 50%;
  max-width: 300px;
}
#contact .content-wrap .col {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#Research {
  background: #BAF1FC;
}
#Research .content-wrap {
  margin: 0;
  flex-flow: row wrap;
}
#Research .content-wrap .interest {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#Research .content-wrap .interest img {
  width: 75%;
  max-width: 200px;
}

@media (max-width: 769px) {
  .title {
    height: 75px;
    margin-top: -5px;
  }
  p {
    font-size: 18px;
  }
  h1 {
    font-size: 45px;
    margin: 10px 0;
  }
  nav {
    min-height: 60px;
    height: 18vw;
    margin: 0;
  }
  nav .content-wrap a {
    min-width: 70px;
    align-items: center;
    width: 24%;
    animation: none;
    transform: translateY(-10px);
  }
  nav h2 {
    font-size: 5vw;
    margin: 10px 0;
  }
  header .content-wrap {
    margin: 0;
  }
  #who .content-wrap {
    padding-top: 0;
    flex-direction: column;
  }
  #who .content-wrap p {
    width: 80vw;
    text-align: center;
  }
  .project {
    margin: 10px;
  }
  #CaseStudies .content-wrap .col {
    width: 200px;
  }
  #CaseStudies .content-wrap .col img {
    width: 150px;
  }
}
@media (max-width: 660px) {
  .project {
    max-width: 350px;
  }
}
#HeaderWave {
  background: #AAA0E7;
}

#WhoWave {
  background: #FFE2C9;
}

#DevWave {
  background: #AEEDB5;
}

#UxWave {
  background: #BAF1FC;
}

.wave-container {
  position: relative;
  width: 105%;
  margin-left: -5px;
}
.wave-container svg {
  margin-bottom: -8vw;
  stroke: #4e0c62;
  stroke-width: 2;
}

/* Animations */
@keyframes nav_slide {
  0% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes nav_out {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-15px);
  }
}
.path-0 {
  animation: pathAnim-0;
  animation-duration: 20s;
  animation-timing-function: linear;
